body {
  font-family: 'Roboto', sans-serif;
  background: #fafafb;
  margin: 0 !important;
}
a {
  color: #5590ad !important;
  text-decoration: none !important;
}
a:hover {
  color: #1b6c95 !important;
  text-decoration: none !important;
}
.navbar {
  height: 64px;
  width: 100%;    
  background-color: #e9ecef !important;
  border-bottom: 1px solid #e2e2e2;
  -webkit-box-shadow: 0 1px 3px rgba(0, 0, 0, .1);
          box-shadow: 0 1px 3px rgba(0, 0, 0, .1);
  background-color: #f3f3f5 !important;
}
.card {
  margin-bottom:15px !important;
  border-radius: 10px;
  -webkit-box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.14);
          box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.14);
}
.main-content {
  margin-bottom: 90px !important;
}
.nav-container {
  display: -ms-flexbox;
  display: flex;
}
.content-container {
  display:-ms-flexbox;
  display:flex;
  -ms-flex-pack:distribute;
      justify-content:space-around;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.column {
  -ms-flex: 1 1;
      flex: 1 1;
  -ms-flex-order: 1;
      order: 1;
  margin:20px;
}
.column-news {
  -ms-flex: 1 1;
      flex: 1 1;
  -ms-flex-order: 2;
      order: 2;
  -ms-flex-positive:2;
      flex-grow:2;
  margin:20px;
}
.column-weather {
  -ms-flex: 1 1;
      flex: 1 1;
  -ms-flex-order: 3;
      order: 3;
  margin:20px;
}
.side-navigation{
  height:100%;
  margin: 0;
}

@media screen and (max-width: 1000px){
  .column-weather{
      display:none;
  }
}
@media screen and (max-width: 1300px){
  .column{
      display:none;
  }
}



